<template>
    <div>
        <div id="sangong">
            <div class="game-container">
                <div class="game-table">
                    <div class="top">
                        <div class="btn">
                            <img src="./../../../assets/圆角矩形1拷贝@2x.png">
                            <img src="./../../../assets/-@2x.png">
                            <img src="./../../../assets/矩形2拷贝@2x.png">
                        </div>
                        <div class="fapai"><img src="./../../../assets/发牌按钮@2x.png"></div>
                    </div>
                    <div class="banker">
                        <div class="banker-card">
                            <img src="./../../../assets/反面.png">
                            <img src="./../../../assets/反面.png">
                            <img src="./../../../assets/反面.png">
                        </div>
                        <div class="banker-info">
                            <div class="banker-head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="banker-name">
                                <p class="banker-nickname">123456456</p>
                                <p>庄家</p>
                            </div>
                        </div>
                    </div>
                    <div class="player">
                        <div class="player-box">
                            <div class="player-card">
                                <img src="./../../../assets/反面.png">
                                <img src="./../../../assets/反面.png">
                                <img src="./../../../assets/反面.png">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">123456456</p>
                                    <p>闲家1</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="bets">

                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {

            }
        },
        mounted: function(){
            this.$store.state.isPageLogin = true;
            document.getElementById('sangong').style.height = window.screen.height + 'px';
        },
        beforeCreate: function() {
            document.getElementsByTagName("body")[0].className="mg0";
            document.getElementsByTagName("html")[0].className="mg0";
        },
        beforeDestroy: function() {
            document.getElementsByTagName("html")[0].removeAttribute("class","mg0");
            document.body.removeAttribute("class","mg0");
        },
    }
</script>

<style scoped>
#sangong{
    background: url('./../../../assets/色彩平衡1@2x.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    overflow-y: auto;
}
.game-container{
    padding: 10px;
}
.top{
    display: flex;
    justify-content: space-between;
}
.btn{
    display: flex;
    flex-wrap: nowrap;
}
.btn img{
    height: 35px;
    width: 35px;
    margin-left: 10px;
    margin-top: 2.5px;
}
.fapai img{
    height: 40px;
}
.banker{
    margin-top: 10px;
    padding: 0 30px;
    display: flex;
    flex-wrap: nowrap;
}
.banker-card{
    position: relative;
    height: 60px;
    width: 90px;
    padding-top: 10px;
}
.banker-card img{
    height: 60px;
    width: 40px;
    position: absolute;
}
.banker-card img:nth-child(2){
    left: 20px;
}
.banker-card img:nth-child(3){
    left: 40px;
}
.banker-info{
    margin-top: 2.5px;
    height: 60px;
    padding: 5px;
    border: 1px solid #AAA3E5;
    display: flex;
    flex-wrap: nowrap;
    border-radius: 5px;
}
.banker-head{
    height: 60px;
    width: 60px;
}
.banker-head img{
    height: 100%;
    width: 100%;
}
.banker-name{
    text-align: left;
    padding: 5px;
    font-size: 14px;
    color: #E7E3F6;
    line-height: 25px;
}
.banker-nickname{
    max-width: 70px;
    overflow: hidden;
}
.banker-name p:last-child{
    font-size: 16px;
}
.player{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.player-box{
    width: 30%;
    min-width: 90px;
}
.player-card{
    position: relative;
    height: 60px;
    width: 90px;
}
.player-card img{
     height: 60px;
    width: 40px;
    position: absolute;
}
.player-card img:nth-child(2){
    left: 20px;
}
.player-card img:nth-child(3){
    left: 40px;
}
.player-info{
    height: 30px;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}
.player-head{
    height: 30px;
    width: 30px;
}
.player-head img{
    height: 30px;
    width: 30px;
}
.player-name{
     text-align: left;
    font-size: 12px;
    color: #E7E3F6;
    line-height: 16px;
    margin-left: 3px;
}
</style>


