<template>
    <div>
        <!-- <mt-header :title="this.$route.query.val.nickname"> -->
        <mt-header title="姓名">
            <router-link to="/friend" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <router-link to="/addFriend" slot="right">
                <mt-button><i class="iconfont icon-yonghu" style="font-size:30px"></i></mt-button>
            </router-link>
        </mt-header>
        <div class="pageContent chat">
            <div id="chat">
                <!-- <div class="list">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯</p>
                        <div class="boo"></div>
                    </div>
                </div>
                <div class="list myList">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯</p>
                        <div class="myboo"></div>
                    </div>
                </div> -->
                <div class="list" v-for="(item,index) in message" :key="index" :class="{myList: item.type}">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>{{item.text}}</p>
                        <div :class="item.type?('myboo'):('boo')"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="user">
            <div class="userContent">
                <mt-field id="sendText" type="textarea" rows="1" v-model="myMsg"></mt-field>
                <mt-button class="send" @click="send">发送</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                myMsg: '',
                message: [],
                websock: null
            }
        },
        methods: {
            send: function(){
                let s = { type: 1, text: this.myMsg};
                // console.log('{'+'"uid":'+1+',"fid":'+2+',"msg":"'+this.myMsg+'","type":"'+"text"+'","username":"'+"abc"+'","fname":"'+"abc"+'"}')
                this.websocketsend('{'+'"uid":'+1+',"fid":'+2+',"msg":"'+this.myMsg+'","type":"'+"text"+'","username":"'+"abc"+'","fname":"'+"abc"+'"}')
                this.message.push(s);
                this.myMsg = ''
            },
            threadPoxi(){  // 实际调用的方法
                //参数
                // const agentData = '{
                //     "uid": 1,
                //     "fid": "",
                //     "msg": "",
                //     "type": "handle",
                // }'
                const agentData = '{'+'"uid":'+1+'"type":"'+"handle"+'","rType":'+1+'}'
                console.log(agentData)
                //若是ws开启状态
                if (this.websock.readyState === this.websock.OPEN) {
                    this.websocketsend(agentData)
                }
                // 若是 正在开启状态，则等待300毫秒
                else if (this.websock.readyState === this.websock.CONNECTING) {
                    let that = this;//保存当前对象this
                    setTimeout(function () {
                        // that.websocketsend(agentData)
                    }, 300);
                }
                // 若未开启 ，则等待500毫秒
                else {
                    this.initWebSocket();
                    let that = this;//保存当前对象this
                    setTimeout(function () {
                        // that.websocketsend(agentData)
                    }, 500);
                }
            },
            initWebSocket(){ //初始化weosocket
                //ws地址
                // const wsuri = process.env.WS_API + "/websocket/threadsocket";
                const wsuri = 'ws://192.168.0.133:2525'
                this.websock = new WebSocket(wsuri);
                this.websock.onmessage = this.websocketonmessage;
                this.websock.onclose = this.websocketclose;
            },
            websocketonmessage(e){ //数据接收
                const redata = JSON.parse(e.data);
                console.log(redata);
                if(redata.role){
                    let s = { type: 0, text: redata.msg};
                    this.message.push(s)
                }
            },
            websocketsend(agentData){//数据发送
                this.websock.send(agentData);
            },
            websocketclose(e){  //关闭
                console.log("connection closed (" + e.code + ")");
            }
        },
        mounted: function() {
            let h = window.screen.height;
            document.getElementById("chat").style.height = h - 137 + "px";
            this.initWebSocket();
            this.threadPoxi()
            // var wsServer = 'ws://192.168.0.133:2525'; //服务器地址
            // var websocket = new WebSocket(wsServer); //创建WebSocket对象
            // alert(websocket.readyState);//查看websocket当前状态
            // websocket.onopen = function (evt) {
            //     //已经建立连接
            //     websocket.send('{"uid":1,"fid":2,"msg":"content","type":"text","format":"json"}');//向服务器发送消息
            // };
            // websocket.onclose = function (evt) {
            // //已经关闭连接
            // };
            // websocket.onmessage = function (evt) {
            // //收到服务器消息，使用evt.data提取
            //     console.log(evt.data)
            // };
            // websocket.onerror = function (evt) {
            // //产生异常
            //  console.log(evt)
            // }; 
        },
        updated: function(){
            if(this.message!=''){
                this.$nextTick(() => {
                    let lastChat = document.getElementById("chat").lastChild;
                    lastChat.scrollIntoView();
                })
            }
        }
    }
</script>

<style scoped>
.user {
  height: 40px;
  position: fixed;
  bottom: 57px;
  width: 100%;
}
.userContent {
  height: 100%;
  border-top: 1px solid #999;
   display: flex;
  justify-content: space-between;
}
#sendText {
  width: 80%;
}
.send {
  width: 20%;
}

.chat{
    overflow: hidden;
    overflow-y: auto;
}
.list{
    padding: 10px 0;
    display: flex;
    flex-wrap: nowrap;
}
.head{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}
.head img{
    width: 100%;
    height: 100%;
}
.msgText{
    font-size: 14px;
    padding: 10px;
    max-width: 60%;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    margin-left: 20px;
    line-height: 20px;
}
.msgText p{
    padding-top: 5px;
}
.boo{
    position: absolute;
    width: 0;
    height: 0;
    border-top: 5px solid transparent; 
    border-right: 10px solid #fff; 
    border-bottom: 5px solid transparent; 
    border-left: 10px solid transparent; 
    top: 30%;
    left: -20px;
}
.myList{
    flex-direction: row-reverse;
}
.myList .msgText{
    margin-left: 0;
    margin-right: 20px;
    background-color: #57d6dd;
    color: #fff;
}
.myList .myboo{
    position: absolute;
    width: 0;
    height: 0;
    border-top: 5px solid transparent; 
    border-right: 10px solid transparent; 
    border-bottom: 5px solid transparent; 
    border-left: 10px solid #57d6dd; 
    top: 30%;
    right: -20px;
}
</style>


